.letter{
    text-align: center;
    font-size: 80px;
}

.box > picture > img {
    display: block;
	max-width:100%;
    height: auto; 
}

.gallery-background{
    background: linear-gradient(to top, $background9 60% , $background7 40%);
}

.container.gallery-container{   
    padding-bottom: 123px;
}

.heading{
    text-align: center;
}

.masonry-list{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    list-style-type: none;   
}

@media screen and (max-width: 479px) {
    .masonry-list {
    max-width: 288px;
    height: 100%;
    }
}
@media screen and (max-width: 767px) {
    .masonry-list{
    max-width: 100%;
    height: 100%;
    }
}
@media screen and (min-width: 768px) and (max-width: 1199px) {
    .masonry-list {
    width: 100%;
    height: 100%;
    }
}

.masonry-item{
    margin-right: 6px;
}


.wrapper4{
    margin-bottom: -43px;
}

.wrapper5{
    margin-bottom: 89px;
}

.wrapper6{
    margin-bottom: -12px;
}

.box{
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    border: 1px solid transparent;
    border-radius: 25px;
    overflow: hidden;
}



.box:hover,
.box:focus{
    cursor: pointer;
    transform: scale(1.1);
    z-index: 2;
}

.box5{
    display: inline-block;
}

.box2,
.box4,
.box6,
.box8{
    margin-bottom: 6px;
}